<template>
<div>
  <el-breadcrumb separator-class="el-icon-arrow-right"  class="crumb">
    <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
    <el-breadcrumb-item>店铺信息</el-breadcrumb-item>
  </el-breadcrumb>
  <!--修改-->
  <div style="margin-top: 50px">


    <el-form :model="formData"  ref="formData" label-width="110px" class="demo-formData">
      <el-form-item label="店铺名称: "  :class="{flo:ismobile}" prop="name">
        <el-input v-model="formData.name" disabled></el-input>
      </el-form-item>

      <el-form-item label="所在商圈: ":class="{flo:ismobile}" prop="area_id">
        <el-input v-model="formData.area" disabled></el-input>
      </el-form-item>
      <el-form-item label="详细地址: ":class="{flo:ismobile}" prop="address">
        <el-input v-model="formData.address" disabled></el-input>


      </el-form-item>
      <el-form-item label="联系人: " :class="{flo:ismobile}" prop="link_user">
        <el-input v-model.number="formData.link_user" disabled></el-input>
      </el-form-item>
      <el-form-item label="联系电话: " :class="{flo:ismobile}" prop="mobile">
        <el-input v-model.number="formData.mobile" disabled></el-input>
      </el-form-item>
    <div style="clear:both"></div>
      <el-form-item label="店铺简介: " prop="recommended">
        <el-input type="textarea" :rows="6" v-model="formData.recommended" disabled></el-input>
      </el-form-item>
      <el-form-item label="备注: " prop="note" >
        <el-input v-model="formData.note" disabled></el-input>
      </el-form-item>
      <el-form-item label="所属分类: ">
        <el-input v-model="formData.category" disabled></el-input>
      </el-form-item>


      <el-form-item label="人均消费: " prop="consumption">
        <el-input v-model="formData.consumption" disabled></el-input>
      </el-form-item>
      <!--<el-form-item label="店铺特点: " style="white-space: nowrap;">-->

        <!--<span>WIFI服务</span>-->
        <!--<el-switch disabled v-model="formData.wifi"></el-switch>-->
        <!--<span>儿童座椅</span>-->
        <!--<el-switch  disabled v-model="formData.child"></el-switch>-->
      <!--</el-form-item>-->
      <!--<el-form-item style="white-space: nowrap;">-->
        <!--<span>停车服务</span>-->
        <!--<el-switch  disabled v-model="formData.pp"></el-switch>-->
        <!--<span>刷卡消费</span>-->
        <!--<el-switch disabled    v-model="formData.isa"></el-switch>-->

      <!--</el-form-item>-->


      <el-form-item label="营业时间: " style="white-space: nowrap;">
        <span v-model="formData.business_start" >{{formData.business_start}}</span>~
        <span v-model="formData.business_end" >{{formData.business_end}}</span>

      </el-form-item>


    </el-form>

  </div>


</div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'
	export default{
		name:'shop',
    data(){
		  return {
		    formData:{},
        ismobile:false

      }
    },
    methods:{
      _isMobile() {
        let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
        return flag;
      },
		  getShopInfo(){
        axios.get(this.$url.getshop,{
          params:{
            token:  sessionStorage.getItem("access-token")
          }
        }).then(
          function (res) {


            this.formData = res.data.data;
            this.formData.area=res.data.data.area.name;
            this.formData.category=res.data.data.category.name;
            var serving=res.data.data.serving.split(",");

            for(let i=0;i<serving.length;i++){
              if(serving[i]==1){
                this.formData.wifi=true;
              }
              if(serving[i]==1){
                this.formData.wifi=true;
              }
              if(serving[i]==2){
                this.formData.child=true;
              }
              if(serving[i]==3){
                this.formData.pp=true;
              }
              if(serving[i]==4){
                this.formData.isa=true;
              }
            };
          }.bind(this)  //改变this指向
        )
      }
    },
    mounted() {
      if (this._isMobile()) {
        this.isCollapse=true
        this.ismobile=false
      } else {
        this.isCollapse=false
        this.ismobile=true
      };
      this.getShopInfo();

    }


	}
</script>

<style>
  .el-table .cell{/*内容过长省略*/
    white-space: nowrap;
    text-align: center;
  }

  .select{margin-top:2rem}
  .table-content{margin-top:50px;
    width: 94%;}
  .el-dialog{width:70%;height:80%;overflow:scroll;}
  .el-dialog .el-form{width:80%;margin:0 auto}
  .el-form-item{width:100%;text-align:left}
  .flo{width:45%;margin-right:5%;margin-top:12px;float:left}
  .el-input.is-disabled .el-input__inner{border:0!important;background:white;color:black}
  .el-textarea.is-disabled .el-textarea__inner{background:white;color:black}

</style>



